webpackJsonp([239731680255039],{

/***/ 3583:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<h1>Components</h1>\n<h4>Motivation</h4>\n<p>Angular contains many <em>schematics</em> for building applications. Components are one such schematic. They encompass a single unit of logic concerned with a single part of the application. Components often partner with other schematics to operate more effectively.</p>\n<p>Among all schematics, components tend to consume more than provide. While other schematics like directives, pipes, and services offer utility, components utilize. They are responsible for the application interface so it makes sense why the consume utility.</p>\n<p>Components simplify the application. Funneling logic into a single section of the visible interface is their primary goal. To build applications step-by-step, you must build component-by-component. Components act as Angular’s building blocks after all.</p>\n<h4>Components Introduction</h4>\n<p>As mentioned, components consume utility (services/resources). They stand between business logic and presentation to produce a cohesive unit. Angular attaches various mechanisms to each component. These attachments identify a class as a component and define its standard capabilities.</p>\n<p>Angular must recognize components when it comes across them. To do this, <code>@Component</code> must decorate every class intended to be a component. Decorators indicate to Angular what the class’ is.</p>\n<p>In the case of a component, it must know how to interact with its injector, connect with a template, pull from a list of styles, encapsulate its styles, and so on. Angular takes care of most of the low-level requirements. Developers still need to configure a component’s behavior, import its dependencies, and extend its logic.</p>\n<p>For all such things, we have the component’s class. The class keeps everything relatively uniform. It encapsulates the component’s business logic.</p>\n<h4>Component Class and Metadata</h4>\n<p>Go ahead and install the <a href=\"https://cli.angular.io\">Angular command-line interface (CLI)</a>. You can learn more about it from <a href=\"https://guide.freecodecamp.org/angular/command-line-interface\">this article</a>. The CLI command <code>ng generate component [name-of-component]</code> yields the following.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-typescript\"><code><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Component<span class=\"token punctuation\">,</span> OnInit <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@angular/core'</span><span class=\"token punctuation\">;</span>\n\n@<span class=\"token function\">Component</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  selector<span class=\"token punctuation\">:</span> <span class=\"token string\">'app-example'</span><span class=\"token punctuation\">,</span>\n  templateUrl<span class=\"token punctuation\">:</span> <span class=\"token string\">'./example.component.html'</span><span class=\"token punctuation\">,</span>\n  styleUrls<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token string\">'./example.component.css'</span><span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">ExampleComponent</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">OnInit</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">ngOnInit</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>This is the basic skeleton from which all great components originate. The <code>@Component</code> decorator is the most important part. Without it, the above example becomes a generic class. Angular relies on decorators to discern a class’ schematic type.</p>\n<p><code>@Component</code> receives metadata as a single object. Decorators are just JavaScript functions under the hood. They take in arguments as with the metadata object. The metadata object configures a component’s basic dependencies. Each fields plays a role.</p>\n<ul>\n<li>\n<p><code>selector:</code> tells Angular to associate the component with a certain element in the application’s template HTML.</p>\n</li>\n<li>\n<p><code>templateUrl:</code> accepts the file location of the component’s template HTML (this is where data gets displayed to).</p>\n</li>\n<li>\n<p><code>styleUrls:</code> accepts an array of style-sheet file locations (strings). These style-sheets target the component’s assigned template.</p>\n</li>\n</ul>\n<p>Think of metadata as a big blob of configuration. The decorator takes it so that it can generate the data specific to the component. The decorator <em>decorates</em> the underlying class with data necessary for its class’ behavior. A <em>component</em> class that is.</p>\n<p>The class’ signature exports by default so that the component can be imported. <code>ngOnInit</code> also gets implemented. <code>implements</code> tells the class to define certain methods per the interface’s definition. <code>ngOnInit</code> is a lifecycle hook.</p>\n<h4>Component Lifecycle and Change Detection</h4>\n<p>Components use all sorts of tools, services, and feature. One key feature available to components is lifecycle hooks. An explanation for each hook exists <a href=\"https://guide.freecodecamp.org/angular/lifecycle-hooks\">on this article</a>.</p>\n<p>There are a eight in total and they all serve as timing functions. They execute conditionally as the component transitions from state-to-state via <a href=\"https://blog.angularindepth.com/everything-you-need-to-know-about-change-detection-in-angular-8006c51d206f\">change detection</a>. This process happens constantly across the component tree. It searches for changes in data which merit a re-rendering of the template.</p>\n<p>Time to move on. Please refer to the aforementioned articles for more information on the component lifecycle. It deserves much more explanation.</p>\n<h4>Component Data</h4>\n<p>Data drives everything. Components are no exception. Components encapsulate all their data. To receive data externally,  a component must explicitly declare it. This form of privacy keeps information from clashing across the component tree.</p>\n<p>Data determines what gets displayed from the component class to its template. Any updates to the class’ data will (or at least should) update the template display.</p>\n<p>Components will often initialize a set of members (or variables) that store data. They are used throughout the component class logic for convenience. This information fuels the logic resulting in the template and its behavior. See the following example.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-typescript\"><code><span class=\"token comment\" spellcheck=\"true\">// ./components/example/example.component.ts</span>\n\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Component<span class=\"token punctuation\">,</span> OnInit <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@angular/core'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Post<span class=\"token punctuation\">,</span> DATA <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'../../data/posts.data'</span><span class=\"token punctuation\">;</span>\n\n@<span class=\"token function\">Component</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  selector<span class=\"token punctuation\">:</span> <span class=\"token string\">'app-example'</span><span class=\"token punctuation\">,</span>\n  templateUrl<span class=\"token punctuation\">:</span> <span class=\"token string\">'./example.component.html'</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">ExampleComponent</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">OnInit</span> <span class=\"token punctuation\">{</span>\n  username<span class=\"token punctuation\">:</span> <span class=\"token keyword\">string</span><span class=\"token punctuation\">;</span>\n  totalPosts<span class=\"token punctuation\">:</span> <span class=\"token keyword\">number</span><span class=\"token punctuation\">;</span>\n  allPosts<span class=\"token punctuation\">:</span> Post<span class=\"token punctuation\">[</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token function\">deletePost</span><span class=\"token punctuation\">(</span>index<span class=\"token punctuation\">:</span> <span class=\"token keyword\">number</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span> <span class=\"token keyword\">void</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>allPosts<span class=\"token punctuation\">.</span><span class=\"token function\">splice</span><span class=\"token punctuation\">(</span>index<span class=\"token punctuation\">,</span> <span class=\"token number\">1</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>totalPosts <span class=\"token operator\">=</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>allPosts<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">ngOnInit</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">:</span> <span class=\"token keyword\">void</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>username <span class=\"token operator\">=</span> DATA<span class=\"token punctuation\">.</span>author<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>totalPosts <span class=\"token operator\">=</span> DATA<span class=\"token punctuation\">.</span>thePosts<span class=\"token punctuation\">.</span>length<span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>allPosts <span class=\"token operator\">=</span> DATA<span class=\"token punctuation\">.</span>thePosts<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token comment\" spellcheck=\"true\">&lt;!-- ./components/example/example.component.html --></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>{{ username }}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>span</span><span class=\"token punctuation\">></span></span>Change Name: <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>span</span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>input</span> <span class=\"token attr-name\">[(ngModel)]</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>username<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h3</span><span class=\"token punctuation\">></span></span>Posts: {{ totalPosts }}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h3</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>ul</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>hr</span><span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">*ngFor</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>let post of allPosts; let i<span class=\"token punctuation\">=</span>index<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>button</span> <span class=\"token attr-name\">(click)</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>deletePost(i)<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>DELETE<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>button</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h6</span><span class=\"token punctuation\">></span></span>{{ post.title }}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h6</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>{{ post.body }}<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>hr</span><span class=\"token punctuation\">/></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>ul</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Note the ways the component interacts with its data. It first fetches it from <code>../../data/posts.data</code> before it begins to forward it to the template for display.</p>\n<p>The data shows up throughout the template. Inside the double curly braces, a variable’s value is mapped from the component class into the braces. The <code>*ngFor</code> loops across the <code>allPosts</code> class array. Clicking on the button removes a specific element from <code>allPosts</code> by its index. You can even change the topmost <code>username</code> by typing into the input box.</p>\n<p>The above interactions alter the component class’ data which in turn updates the component’s template HTML. Components provide the backbone logic that facilitates the flow of data. The template HTML makes that data readable to the user.</p>\n<h4>Component Template</h4>\n<p>The previous example’s template HTML featured an interesting syntax. The syntax was not actual HTML. It was Angular’s template HTML. Some often refer to it has HTML <em>Plus</em> recognizable only by Angular’s compiler. The compiler supports a syntax resulting in the dynamic manipulation of HTML. This article will often refer to it as ‘template HTML’ or ‘template’.</p>\n<p>The syntax lets components inject data directly into the template HTML. The injection is dynamic. Meaning, data can iterate and display itself as HTML without needing external assistance. The Angular compiler compiles it into real HTML by the time it reaches the web browser.</p>\n<p>To learn more about some of the ways data binds to the template, read about <a href=\"https://guide.freecodecamp.org/angular/data-binding\">data binding in Angular</a>. A few examples of data binding occurred in the previous example (<code>{{ ... }}</code>). For this article, it is enough to recognize data interactions were happening between the component class and its template.</p>\n<h4>Querying the Template</h4>\n<p>Data managing the state of the template imperatively works OK. Yet, pure data does not always fulfill an application’s intended design. Interacting more directly with the Document Object Model (DOM) may be required.</p>\n<p>To do that, the component must have reference to the template elements. When the data changes, the component can manipulate the DOM explicitly. This is a more declarative approach.</p>\n<p>Components can grab references using a web browser’s DOM application programming interface (API). Bad idea though. Angular prefers cross-platform compatibility. For a component to function outside of the web browser, it needs to use Angular’s API instead of the DOM’s.</p>\n<p>Components can query their templates using the <code>@ViewChild</code> and <code>ContentChild</code> decorators. They grab references to template elements on behalf of the component class.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-typescript\"><code><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Component<span class=\"token punctuation\">,</span> ViewChild<span class=\"token punctuation\">,</span> ContentChild<span class=\"token punctuation\">,</span> ElementRef<span class=\"token punctuation\">,</span> Renderer2<span class=\"token punctuation\">,</span> AfterContentChecked<span class=\"token punctuation\">,</span> AfterViewChecked <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'@angular/core'</span><span class=\"token punctuation\">;</span>\n\n@<span class=\"token function\">Component</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  selector<span class=\"token punctuation\">:</span> <span class=\"token string\">'app-child'</span><span class=\"token punctuation\">,</span>\n  template<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`\n  &lt;button (click)=\"toggleEnlarge()\">Toggle Enlarge&lt;/button>\n  &lt;ng-content>&lt;/ng-content>\n  `</span></span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">ChildComponent</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">AfterContentChecked</span> <span class=\"token punctuation\">{</span>\n  @<span class=\"token function\">ContentChild</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"pReference\"</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> read<span class=\"token punctuation\">:</span> ElementRef <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> pElement<span class=\"token punctuation\">:</span> ElementRef<span class=\"token punctuation\">;</span>\n  textEnlarge<span class=\"token punctuation\">:</span> <span class=\"token keyword\">boolean</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">false</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">private</span> renderer<span class=\"token punctuation\">:</span> Renderer2<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">toggleEnlarge</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textEnlarge <span class=\"token operator\">=</span> <span class=\"token operator\">!</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textEnlarge<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">ngAfterContentChecked</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textEnlarge<span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>renderer<span class=\"token punctuation\">.</span><span class=\"token function\">setStyle</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>pElement<span class=\"token punctuation\">.</span>nativeElement<span class=\"token punctuation\">,</span> <span class=\"token string\">'font-size'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'25px'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n      <span class=\"token keyword\">else</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>renderer<span class=\"token punctuation\">.</span><span class=\"token function\">setStyle</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>pElement<span class=\"token punctuation\">.</span>nativeElement<span class=\"token punctuation\">,</span> <span class=\"token string\">'font-size'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'initial'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n@<span class=\"token function\">Component</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  selector<span class=\"token punctuation\">:</span> <span class=\"token string\">'app-parent'</span><span class=\"token punctuation\">,</span>\n  template<span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`\n  &lt;button (click)=\"toggleHighlight()\">Toggle Highlight&lt;/button>\n  &lt;h1 #hOneRefereance>View Child&lt;/h1>\n  &lt;app-child>\n    &lt;p #pReference>Content Child&lt;/p>\n  &lt;/app-child>\n  `</span></span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">ParentComponent</span> <span class=\"token keyword\">implements</span> <span class=\"token class-name\">AfterViewChecked</span> <span class=\"token punctuation\">{</span>\n  @<span class=\"token function\">ViewChild</span><span class=\"token punctuation\">(</span><span class=\"token string\">\"hOneRefereance\"</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> read<span class=\"token punctuation\">:</span> ElementRef <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> hOneElement<span class=\"token punctuation\">:</span> ElementRef<span class=\"token punctuation\">;</span>\n  textHighlight<span class=\"token punctuation\">:</span> <span class=\"token keyword\">boolean</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">false</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">constructor</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">private</span> renderer<span class=\"token punctuation\">:</span> Renderer2<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">toggleHighlight</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textHighlight <span class=\"token operator\">=</span> <span class=\"token operator\">!</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textHighlight<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">ngAfterViewChecked</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>textHighlight<span class=\"token punctuation\">)</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>renderer<span class=\"token punctuation\">.</span><span class=\"token function\">setStyle</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>hOneElement<span class=\"token punctuation\">.</span>nativeElement<span class=\"token punctuation\">,</span> <span class=\"token string\">'background-color'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'yellow'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token keyword\">else</span>\n      <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>renderer<span class=\"token punctuation\">.</span><span class=\"token function\">setStyle</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>hOneElement<span class=\"token punctuation\">.</span>nativeElement<span class=\"token punctuation\">,</span> <span class=\"token string\">'background-color'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'initial'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>The above example contains two buttons that toggle a certain style for each element. Clicking the buttons toggles the true/false values unique to each component. These booleans determine if the custom styles apply. Instead of these values causing changes imperatively, the lifecycle hooks (<code>ngAfterViewChecked</code> and <code>ngAfterContentChecked</code>) declaratively alter the DOM.</p>\n<p>The declarative approach explicitly changes the style through the element’s reference. In imperative programming, changes to the DOM based off data are implicit. Check out this article on <a href=\"https://codeburst.io/declarative-vs-imperative-programming-a8a7c93d9ad2\">imperative and declarative programming</a> to learn more.</p>\n<p>The main thing to notice is how these references get pulled from the template. In the example, there are two sections of the template queried using two decorators: <code>@ViewChild</code> and <code>@ContentChild</code>.</p>\n<p>They differ in where they look for an element’s reference whether it be in the content DOM or view DOM. These two DOMs exist in ParentComponent’s template. Differentiating between them is important because they finish rendering at separate times.</p>\n<p>This is why <code>@ViewChild</code> and <code>@ContentChild</code> both exist. They work together with their companion lifecycle hooks <code>ngAfterViewChecked</code> and <code>ngAfterContentChecked</code>. These lifecycle hooks wait for their respective queries to resolve before executing.</p>\n<p>Once resolved, <code>@ViewChild</code> and <code>@ContentChild</code> provide references to two elements. Both exist in separate parts of the DOM. The boolean data still determines the outcome. How that outcome translates to the DOM is the key difference from before. The DOM updates via<code>Renderer2</code>’s direct manipulation of it.</p>\n<h4>Content Projection</h4>\n<p>The content DOM exists in the innerHTML of ChildComponent’s <code>&#x3C;app-child>&#x3C;/app-child></code> element. It is all positioned within ParentComponent’s template. The innerHTML of <code>app-child</code> <em>projects</em> onto ChildComponent’s template through <code>&#x3C;ng-content>&#x3C;/ng-content></code>.</p>\n<p>This exemplifies content projection. Displaying content from <code>one</code> component to another using the innerHTML of <code>another</code>’s tags in <code>one</code>’s template so that <code>another</code> component can pull that innerHTML into its own template via <code>&#x3C;ng-content>&#x3C;/ng-content></code>. <em>Thank you for reading that sentence.</em></p>\n<p>Hence why ChildComponent references its <code>&#x3C;p>&#x3C;/p></code> element using <code>@ContentChild</code>. Content contained within <code>&#x3C;app-child>&#x3C;/app-child></code> in ParentComponent’s template makes up the content DOM. ChildComponent references the element with an <code>@ContentChild</code> query.</p>\n<p>ParentComponent’s view DOM consists of everything accessible from within the component’s view. This does not necessarily include the entire template given the innerHTML of <code>&#x3C;app-child>&#x3C;/app-child></code>. Again, this part of the DOM is queried from ChildComponent using <code>@ContentChild</code>. Everything else gets queried using <code>@ViewChild</code> from the ParentComponent class.</p>\n<p>This is a great way for components to exchange content and query its own content regardless of its DOM type. Components can communicate with itself and others using data binding as well. Read more about it from <a href=\"https://guide.freecodecamp.org/angular/data-binding\">this article</a>.</p>\n<h4>Component Styles</h4>\n<p>Styles are critical to a component’s readability and interactivity. Each component encapsulates its style-sheet dependencies. That way they only apply to the component’s template HTML. A special technique introduced by HTML’s shadow DOM makes this possible.</p>\n<p>A shadow DOM branch may exist on any element. This part of the DOM cannot be seen from the HTML’s source code. Standard HTML elements leverage the shadow DOM to provide their trademark appearances. A shadow DOM branch must anchor itself to a visible component so that it can style and customize it.</p>\n<p>The unique aspect about a shadow DOM branch is its encapsulation. Everything used to style a shadow DOM branch’s root element is private to it. No other element can access it.</p>\n<p>Angular embraces this form of encapsulation with components. The style-sheet and template of a component encapsulate together. No other components have access to them. Style-sheet clashes cannot occur.</p>\n<p>Angular does not use the shadow DOM by default. It uses an emulation system that mimics the behavior of the shadow DOM. This is a temporary measure since some web browsers do not yet support the shadow DOM API.</p>\n<p>The <code>@Component</code> metadata contains the <code>encapsulation</code> field. This lets developers toggle in-between emulated shadow DOM, real shadow DOM, or neither. Here are the options in their respective order:</p>\n<ul>\n<li>\n<p><code>ViewEncapsulation.Emulated</code> - fake shadow DOM</p>\n</li>\n<li>\n<p><code>ViewEncapsulation.Native</code> - real shadow DOM</p>\n</li>\n<li>\n<p><code>ViewEncapsulation.None</code> - neither</p>\n</li>\n</ul>\n<p><code>ViewEncapsulation.None</code> means the component’s style-sheets elevate to the global scope. Not recommended considering components should form their own private unit (encapsulation). Angular still provides it as an escape hatch for extreme situations.</p>\n<h4>Conclusion</h4>\n<p>Components build applications. They are privately scoped and separately uniform of each other unless configured otherwise. Applications tend to begin from the root module. Past that, components form an elongated tree defining the rest of the application.</p>\n<p>A components covers a designated unit of the application interface. This includes its styles, logic, and layout. Other schematics such as pipes, services, and directives see frequent use in component code. You can learn more about these interactions in some of the other Angular guide articles.</p>\n<p>Do not forget that components must <a href=\"https://angular.io/guide/bootstrapping\">bootstrap</a>. This can happen in the root module or the component’s metadata. This is so Angular recognizes the component wherever it appears in the application.</p>\n<p>You can always learn more by exploring the below links leading. Component carry far more depth than that what this article could convey.</p>\n<h2>Sources</h2>\n<ul>\n<li><a href=\"https://angular.io/guide\">Angular Team. “Angular Docs”. <em>Google</em>. Accessed 3 June 2018</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/Web_Components/Using_shadow_DOM\">Mozilla MDN Community. “Using Shadow DOM”. <em>Mozilla</em>, updated 30 May 2018. Accessed 3 June 2018</a></li>\n<li><a href=\"https://codeburst.io/declarative-vs-imperative-programming-a8a7c93d9ad2\">Mundy, Ian. “Declarative vs Imperative Programming”. <em>codeburst.io</em>, 20 Feb. 2017. Accessed 3 June 2018</a></li>\n</ul>\n<h2>Resources</h2>\n<ul>\n<li><a href=\"https://angular.io/guide\">Angular Documentation</a></li>\n<li><a href=\"https://github.com/angular/angular\">Angular GitHub Repository</a></li>\n<li><a href=\"https://angular.io/guide/architecture-components\">Intro to Components</a></li>\n<li><a href=\"https://angular.io/guide/displaying-data\">Components in Depth</a></li>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/Web_Components/Using_shadow_DOM\">Shadow DOM</a></li>\n</ul>","fields":{"slug":"/angular/components/"},"frontmatter":{"title":"Components"}}},"pathContext":{"slug":"/angular/components/"}}

/***/ })

});


